*
{
  margin: 0;
  padding: 0;
}
body
{
  background: linear-gradient(90deg, #d9f073 0%, #4acfe7 60%);
}
.App-input
{
  text-align: center;
  height: 300px;
  color: rgba(228, 8, 203, 0.508);
  font-size: 18px;
  padding: 10px;
}
.App-input input
{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid grey;
  outline: none;
  padding: 10px 5px;
  margin-top: 15px;
  width: 200px;
}
.App-input button{
  background-color: lightblue;
  color: rgb(17, 2, 2);
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 15px;
  font-weight: 700;
}
nav{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
  background-color: rgba(216, 227, 11, 0.592);
  margin-top: 30px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: rgba(20, 9, 23, 0.777);
  font-weight: 700;
}
nav ul li
{
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.4);
  color: white;
}
.navActive
{
  color: rgb(230, 34, 34);
}
.App-container
{
  background-color: #4acfe7;
  color: black;
  padding: 10px;
}